import React from 'react'
import { Link } from 'react-router-dom';
import Select from 'react-select';

import ViewList from './viewList'
import Modal from '../modal'
import axios from '../../API.js'

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      viewdata: [],
      tempValue: '', tempid: '',
      selectedOption: null, modalopen: false,
      filename: '', filepath: '', entertext: '', isError: false,
      error: { entertext: '' },
      successMsg: '', imgWidth: null
    }
  }
  clickView = (res) => {
    this.setState({ modalopen: !this.state.modalopen, filepath: res.filepath, entertext: res.entertext, tempid: res.id })
  }
  modalClick = (status) => {
    // console.log(status)
    // this.setState({tempValue:status})
    this.onUpdate(status)
  }
  clickRestart = () => {
    this.setState({ modalopen: !this.state.modalopen })
  }

  handleChange = selectedOption => {
    this.setState({ selectedOption });
    console.log(`Option selected:`, selectedOption);
  };
  onUpdate = (tempValue) => {
    let data = this.state
    data.userid = "1"
    data.status = tempValue
    data.id = this.state.tempid
    axios.post("/userWorks/Update", data).then(res => {
      if (res.data.status === 'success') {
        //this.props.history.push('/Requesters')
        window.location.reload()
        alert('Requester Details Updated Successfully..!!')
      }
    })
  }
  onChange = (e) => {
    const index = e.target.selectedIndex;
    const optionElement = e.target.childNodes[index];
    const optionElementId = optionElement.getAttribute('id');
    console.log(optionElementId, e.target.value)
    this.setState({ tempid: optionElementId, tempValue: e.target.value })
  }
  addnew = () => {
    this.props.history.push('/requester/add');
  }
  deleteItem = (id) => {
    var r = window.confirm('Are u want to Delete?')
    if (r === true) {
      axios.post("/Requesters/Update", { id: id, action: 'del', userid: this.context.userdata.userid }).then(res => {
        if (res.status === 'success')
          this.loadDate();
      })
    }
  }
  loadDate = () => {
    axios.get("/userWorks").then(res => {
      if (res.data.status === 'success')
      console.log(res)
        this.setState({ viewdata: res.data.data })
    })
  }
  componentDidMount = () => {
    this.loadDate();
  }
  render() {
    const typingsuccess1 = (
      <>
        <div className="row" style={{ padding: '1em' }}>
          <div className="col-6">
            <div className="container-outer mb-4">
              <img id="map" src={this.state.filepath} width={this.state.imgWidth === null ? "100%" : this.state.imgWidth} />
            </div>
          </div>

          <div className="col-6">

            <textarea name="entertext" className="col-12 mb-4" placeholder="Please type here" style={{ height: '24em' }} onChange={this.onChange} value={this.state.entertext}></textarea>
            {this.state.error.entertext}
            {this.state.successMsg !== '' ? <div className="success"><i className="fa fa-check" aria-hidden="true"></i> {this.state.successMsg}</div> : ''}

          </div>

        </div>
      </>
    )

    const { selectedOption } = this.state;
    const options = [
      { value: 'Select', label: 'Select' },
      { value: 'Accepted', label: 'Accepted' },
      { value: 'Rejected', label: 'Rejected' },
    ];

    const data = {
      columns: [
        {
          label: 'ID',
          field: 'id',
          sort: 'asc',
        },
        {
          label: 'Email',
          field: 'email',
          sort: 'asc',
        },
        {
          label: 'Work Type',
          field: 'worktype',
          sort: 'asc',
        },
        {
          label: 'Date',
          field: 'date',
          sort: 'asc',
        },
        {
          label: 'Time',
          field: 'time',
          sort: 'asc',
        },
        {
          label: 'Status',
          field: 'status',
        },
        // {
        //   label: 'Change Status',
        //   field: 'changestatus',
        // },
        {
          label: 'Action',
          field: 'action',
        }],
      rows: [...this.state.viewdata.map((res, i) => ({
        id: res.id,
        email: res.email,
        worktype: res.workType,
        date: res.date1,
        time: res.time1,
        status: res.isAccept ? 'Accepted' : res.isReject ? 'Rejected' : 'Pending',
        // changestatus: <><select
        //   onChange={this.onChange}
        //   options={options}
        // ><option id={res.id} value="Select">Select</option>
        //   <option id={res.id} value="Accepted">Accepted</option>
        //   <option id={res.id} value="Rejected">Rejected</option> </select></>,
        action: <><div className="btn btn-primary btn-sm" onClick={() => this.clickView(res)}>View</div></>
        // <div className="btn btn-danger btn-sm" onClick={() => this.onUpdate(res.id)}>Submit</div>&nbsp;
      }))]
    }
    let view = (
      <>

        <div className="menubar">
          {this.state.modalopen ? <Modal open={true} button1='Accept' clickButton1={() => this.modalClick('Accepted')} button2='Reject' clickButton2={() => this.modalClick('Rejected')} button3='Close' clickButton3={this.clickRestart} modalheader="Quality Checking" modalbody={typingsuccess1} /> : ''}

          <ViewList header="Work Status" data={data} />
        </div>

      </>
    )
    return view;
  }
}
export default App;